@font-face {
  font-family: 'AkiraExpanded';
  src: local('AkiraExpanded'), url('./fonts/AkiraExpanded.otf') format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}
*{
  font-family: 'AkiraExpanded' ;
}
/*Helvetica*/

.background-promo{
  min-height: 65vh;
  background-size: cover;
  background-position: center;
}
#root{
  overflow: hidden;
}
.logo{
  height:155px;
}

.header-ski{
  display:table;
  width: 100vw;
}
.logo-holder{
  float:left;
  margin-bottom:-50px;
  margin-top:-15px;
}

.rest-holder{
  display: table;
  float:right;
  position: absolute;
  top: 65px;
  right: 0px;
}
.rest-holder div{
  float:right;
  margin-right:25px;
  color:black;
}
.invisible{
  display:none;
}

.original-skiers{
  text-align: center;
  color:black;
  font-size:38px;
  margin-bottom:-10px;
  line-height: 1;
}

.avax-blockchain-skiers{
  text-align: center;
  color:red;
  font-size:38px;
}

.install-metamask{
  padding:8px;
  padding-top:10px;
  padding-bottom:10px;
  border-top:1px solid black;
  border-bottom:1px solid black;
  border-left:2px solid black;
  border-right:2px solid black;
  font-family:'Helvetica';
  text-align: center;
  color:black;
  border-radius:5px;
  font-weight:bold;
  font-size:22px;
}

.install-metamask:hover{
  color:black;
}

.time-till-mint{
  color:black;
  text-align: center;
  font-size: 28px;
}

.meet-skiers{
  font-size:38px;
}

.meet-skiers-text{
  font-family:'Helvetica';
  font-weight: 600;
}

.minted-component{
  color:black;
  font-size:22px;
}

.skier-select{
  padding:1px;
  border-top:1px solid black;
  border-bottom:1px solid black;
  border-left:2px solid black;
  border-right:2px solid black;
  background:black;
  border-radius:5px;
  display: table;
  float:left;
  margin-right:15px;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
          user-select: none; /* Non-prefixed version, currently
                                supported by Chrome, Edge, Opera and Firefox */
}

.skier-select *{
  color:white !important;
  float:left;
  margin-right:7px;
  margin-left:7px;
}

.skier-jumpin{
  padding:8px;
  padding-top:10px;
  padding-bottom:10px;
  border-top:1px solid black;
  border-bottom:1px solid black;
  border-left:2px solid black;
  border-right:2px solid black;
  border-radius:5px;
  font-size:14px;
}

.skier-price{
  color:black;
  font-family:'Helvetica';
  font-weight: 500;
  position: absolute;
  width: 127px;
  margin-top:3px;
}

.meet-skiers-container,.tokenomics-container,.roadmap-container {
  padding-top:10vh;
  padding-bottom:10vh;
  padding-left:25px;
  padding-right:25px;
  background-attachment: fixed;
  background-size: cover;
  min-height: 200px;
}

.tokenomics-container{
  padding-top:10vh;
  padding-bottom:10vh;
  padding-left:25px;
  padding-right:25px;
  min-height: 200px;
}

.roadmap-title{
  color:#a0f5f8;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 0 1px black, 0 0 1px black, 0 0 1px black, 0 0 1px black;
  -webkit-font-smoothing: antialiased;
}

.roadmap-paragraphs{
  color:white;
  font-family:'Helvetica';
  text-shadow: 0 0 1px black, 0 0 1px black, 0 0 1px black, 0 0 1px black;
  -webkit-font-smoothing: antialiased;
    text-align: center;
}

.menu-link,.menu-link:hover{
  color:black;
}
.menu-link.disabled{
  opacity:0.5;
}